<template>
    <div class="container">
        <button @click="onClick">parent:{{show}}</button>
        <edit-post-modal  v-model="show" @clicka="(val)=>{show=val}"/>
    </div>
</template>

<script>
import store from '../store.js'
import EditPostModal from './EditPostModal'
export default {
    // importname
    name:'Ping',
    data(){
        return {
            show:false,
            sharestate:store.state,
            title:'',
            textbody:'',
            editForm:{
                title:'123',
                summary:'321',
                body:'1234567',
                errors:0,
                titleError:null,
                bodyError:null
            }

        }
    },
    components:{
        EditPostModal
    },
    methods:{
        onClick(){
            this.show = !this.show
        }
    }
}
</script>


