<template>
    <div class="container">
        <!-- --------------------Modal Edit------------------------ -->
        <div class="modal fade" id="exampleModal" tabindex="-1" role="dialog" data-backdrop="static" aria-labelledby="exampleModalLabel" aria-hidden="true">
        <div class="modal-dialog modal-lg modal-dialog-centered" role="document">
            <div class="modal-content">
            <div class="modal-header">
                <h5 class="modal-title" id="exampleModalLabel">Update Post</h5>
                <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                <span aria-hidden="true">&times;</span>
                </button>
            </div>
            <div class="modal-body">
                <form @submit.prevent="onSubmitUpdate" @reset.prevent="onResetUpdate">
                <div class="form-group">
                    <input type="text" v-model="editForm.title" v-bind:class="{'form-control':true,'is-invalid':editForm.titleError }" id="edit_title" placeholder="" required>
                    <div class="invalid-feedback">{{editForm.titleError}}</div>
                </div>
                <div class="form-group">
                    <input type="text" v-model="editForm.summary" class="form-control" id="edit_summary" placeholder="">
                </div>
                <div class="form-group">
                    <textarea v-model="editForm.body" id="edit_body" rows="5" class="form-control"></textarea>
                    <div class="invalid-feedback">{{editForm.bodyError}}</div>
                </div>
                <div class="modal-footer">
                    <button type="button" class="btn btn-secondary" data-dismiss="modal">Cancel</button>
                    <button type="submit" class="btn btn-primary">Update</button>
                </div>

                </form>
            </div>

            </div>
        </div>
        </div><!-- End Modal Edit -->


        <!-- Modal Delete -->
        <div class="modal fade" id="modalDelete" tabindex="-1" role="dialog" aria-labelledby="modalDeleteLabel" aria-hidden="true">
        <div class="modal-dialog modal-dialog-centered" role="document">
            <div class="modal-content">
            <div class="modal-header">
                <h5 class="modal-title" id="modalDeleteLabel">Delete</h5>
                <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                <span aria-hidden="true">&times;</span>
                </button>
            </div>
            <div class="modal-body">
                Are you sure
            </div>
            <div class="modal-footer">
                <button type="button" class="btn btn-secondary" data-dismiss="modal">No</button>
                <button type="button" class="btn btn-primary" @click="onDeletePost">Yes</button>
            </div>
            </div>
        </div>
        </div><!-- End Modal Delete -->



       

        <!-- ---------------Card-------------- -->
        <div class="card border-0 mb-5">
            <!--  -->
            <div class="card-header d-flex align-items-center justify-content-between bg-light border-0 mb-3">
                <div class="float-left"> <!-- posts -->
                    <i class="fa fa-commenting-o"></i>
                    All Posts<small class="font-weight-normal">{{posts._meta?posts._meta.total_items:''}}{{posts._meta?posts._meta.total_pages:''}}</small>
                </div>
                <div class="dropdown"> <!--  -->
                    <div class="" id="dropdownMenuButton" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                        <i class="fa fa-bars"></i>
                    </div>
                    <div class="dropdown-menu dropdown-menu-right" aria-labelledby="dropdownMenuButton">
                        <a href="#" class="dropdown-item">haha</a>
                    </div>

                </div>
            </div>
            <!-- posts -->
            <div v-if="posts">
            <div class="card-body border mb-3 d-flex align-items-start" v-for="(post,index) in posts.items" v-bind:key="index">
                        <div class=""><!--  -->
                            <img :src="post.author._links.avatar" alt="" class="d-flex mr-4" style="width:50px">
                        </div>
                        <div class="w-75" style="flex:1;"><!--  -->
                            <div class="mb-2 d-flex justify-content-between flex-wrap" ><!--  -->
                                <router-link :to="{name:'Post',params:{id:post.id}}" class="h5 text-truncate w-75">{{post.title}}</router-link>
                                <!-- 
                                    postauthor_id
                                        1.postsauthor
                                -->
                                <div class="d-inline-block  text-truncate" >
                                    <small>{{$moment(post.timestamp).fromNow()}} /</small>
                                    <router-link :to="{name:'Profile',params:{id:post.author.id}}"><small>{{post.author.username||post.author.name}}</small></router-link>
                                </div>
                            </div>
                            <!-- summary -->
                            <div class="d-block text-wrap overflow-hidden mb-2" style="height:3em">
                                {{post.summary}}
                            </div>
                            <!--  -->
                            <div class="d-flex justify-content-between align-items-center">
                                <div class="">
                                    <i class="fa fa-eye text-muted mr-1"></i><small>{{post.views}}</small>
                                </div>
                                <div class="btn-group" role="group">
                                    <button type="button" 
                                        class="btn btn-outline-secondary btn-sm" 
                                        data-toggle="modal" 
                                        data-target="#exampleModal"
                                        :disabled="sharestate.user_id == post.author.id? false:true"
                                        @click="onEditPost(post)"></button>
                                    <button type="button" 
                                        class="btn btn-outline-secondary btn-sm" 
                                        data-toggle="modal" 
                                        data-target="#modalDelete" 
                                        :disabled="sharestate.user_id == post.author.id? false:true"
                                        @click="delPostid = post.id"></button>
                                </div>
                            </div>
                        </div>
            </div> <!-- card body -->
            </div>

            <!-- -------------Pagination-------------- -->
            <nav>
                <ul class="pagination">
                    <!-- 
                         
                            'disabled':posts._meta.page<=1?true:false
                                truefalse<=1
                    -->
                    <li v-bind:class="{'page-item':true,'disabled':posts._meta.page<=1?true:false}">
                        <router-link 
                        :to="{name:'Home',query:{page:posts._meta.page-1 , per_page:posts._meta.per_page}}" 
                        class="page-link" >
                            &laquo;
                        </router-link>
                    </li>
                    <!-- 
                        
                            1.pagepage
                            2.query1
                     -->
                    <li v-bind:class="{'page-item':1,'active':$route.query.page==page || (!$route.query.page && page==1)}" v-for="(page,index) in iter_pages" v-bind:key="index">
                        
                        <router-link v-if="page!='NaN'" :to="{name:'Home',query:{page:page,per_page:posts._meta.per_page}}" class="page-link">{{page}}</router-link>
                        <span class="list-inline-item h-100 ml-2 mr-2 text-secondary" v-else style="line-height:3.5em;">...</span>
                        
                    </li>


                    <li v-bind:class="{'page-item':true,'disabled':posts._meta.page>=posts._meta.total_pages?true:false}">
                        <router-link 
                        :to="{name:'Home',query:{page:posts._meta.page+1 , per_page:posts._meta.per_page}}" 
                        class="page-link" >
                            &raquo;
                        </router-link>
                    </li>
                </ul>
            </nav>
        </div> <!--card -->


        <!-- ---------------------post--------------------- -->
        <!-- 
            .prevent? 
                onSubmitAdd
        -->
        <div class="mb-4 border-bottom ">
            <div class="h5 d-inline-block"></div>
        </div>
        <form v-if="sharestate.is_authenticated" @submit.prevent="onSubmitAdd" class = "mb-4">
            <div class="form-group">
                <input type="text" v-model="postForm.title" v-bind:class="{'form-control':true,'is-invalid':postForm.titleError }" id="post_title" placeholder="" required>
                <div class="invalid-feedback">{{postForm.titleError}}</div>
            </div>
            <div class="form-group">
                <input type="text" v-model="postForm.summary" class="form-control" id="post_summary" placeholder="">
            </div>
            <div class="form-group">
                <textarea v-model="postForm.body" id="post_body" rows="5" class="form-control"></textarea>
                <div class="invalid-feedback">{{postForm.bodyError}}</div>
            </div>
            <button type="submit" class="btn btn-primary">Submit</button>
        </form>
    </div> <!--Container-->
</template>

<script>
import store from '../store.js'
import '../assets/bootstrap-markdown/js/bootstrap-markdown.js'
import '../assets/bootstrap-markdown/js/bootstrap-markdown.zh.js'
import '../assets/bootstrap-markdown/js/marked.js'

export default {
    name:'Home',
    //
    components:{ 
        
    },
    data(){
        return {
            sharestate:store.state,
            posts:{
                _meta:{
                    page:1,
                    per_page:10
                }
            },
            iter_pages:[], //Pagination
            delPostid:'',
            postForm:{
                title:'',
                summary:'',
                body:'',
                errors:0,
                titleError:null,
                bodyError:null
            },
            editForm:{
                title:'',
                summary:'',
                body:'',
                errors:0,
                titleError:null,
                bodyError:null
            }
        }
    },
    methods:{
        //
        onEditPost(post){
            // postFormposteditForm
            //  asign
            this._tearDownFormError('editForm')
            this.editForm = Object.assign(this.editForm,post)
            $("#edit_body").markdown({
                autofocus:false,
                savable:false,
                iconlibrary: 'fa',  // Font Awesome
                language: 'zh'
            })
        },
        //update
        onSubmitUpdate(){
            this._tearDownFormError('editForm')
            const {title,summary,body,id}  = this.editForm
            let path = `/posts/${id}`
            if($.trim(title)=='' || !this.editForm.title){
                this.editForm.errors+=1
                this.editForm.titleError='Please input your title.'
            }
            if($.trim(body)=='' || !this.editForm.body){
                this.editForm.errors+=1
                this.editForm.bodyError='Please input your article.'
                $("#edit_body").parent(".md-editor").addClass("is-invalid")
            }else{
                 $("#edit_body").parent(".md-editor").removeClass("is-invalid")
            }
            if(this.editForm.errors){
                return
            }
            let payload = {
                title:title,
                summary:summary,
                body:body,
                id:id
            }
            this.$axios.put(path,payload).then(res=>{
                //
                //  bootstrap
                $('#exampleModal').modal('hide')
                this.$toasted.success("")
                this._getPosts()
            }).catch(e=>{
                this.$toasted.error("")

            })

        },
        //modal
        onDeletePost(){
            if(!this.delPostid){
                this.$toasted.success("Ooooh No !")
                return
            }
            let path = `/posts/${this.delPostid}`
            this.$axios.delete(path).then(res=>{
                this.$toasted.success("")
                $("#modalDelete").modal("hide")
                this._getPosts()
            }).catch(e=>{
                this.$toasted.error("")
                $("#modalDelete").modal("hide")
                console.log(e)
            })


        },
        //
        onSubmitAdd(){
            this._putPosts()
        },
        _tearDownFormError(witchform){
            switch(witchform){
                case 'editForm':
                    this.editForm.errors=0
                    this.editForm.titleError=''
                    this.editForm.bodyError=''
                    break;
                case 'postForm':
                    this.postForm.errors=0
                    this.postForm.titleError=''
                    this.postForm.bodyError=''
                    break;
                case 'All':
                    this.editForm.errors=0
                    this.editForm.titleError=''
                    this.editForm.bodyError=''
                    this.postForm.errors=0
                    this.postForm.titleError=''
                    this.postForm.bodyError=''
            }
        },
        _putPosts(){
            this.postForm.errors=0
            this.postForm.titleError=''
            this.postForm.bodyError=''
            const {title,summary,body} = this.postForm
            let path = '/posts'
            if($.trim(title)=='' || !this.postForm.title){
                this.postForm.errors+=1
                this.postForm.titleError='Please input your title.'
            }
            if($.trim(body)=='' || !this.postForm.body){
                this.postForm.errors+=1
                this.postForm.bodyError='Please input your article.'
                $("#post_body").parent(".md-editor").addClass("is-invalid")
            }else{
                 $("#post_body").parent(".md-editor").removeClass("is-invalid")
            }
            if(this.postForm.errors){
                return
            }
            let payload = {
                title:title,
                summary:summary,
                body:body,
                author_id:this.sharestate.user_id
            }
            this.$axios.post(path,payload)
            .then(res=>{
                this._getPosts()
                this.postForm.title = ''
                this.postForm.summary=''
                this.postForm.body=''
            })
            .catch(e=>{
                console.log(e.response)
            })

        },
        _getPosts(){
            let page = 1
            let per_page = 4
            //query
            if (this.$route.query.page && this.$route.query.per_page){
                if(this.$route.query.page<1 || 
                this.$route.query.page>this.posts._meta.total_pages || 
                this.$route.query.per_page<1
                ){this.$router.back()}
            }
            if (typeof this.$route.query.page != 'undefined'){
                page = this.$route.query.page
            }
            if (typeof this.$route.query.per_page!='undefined'){
                per_page = this.$route.query.per_page
            }
            let path = '/posts'
            this.$axios.get(path,{
                params:{
                    page:page,
                    per_page:per_page
                }
            }).then(res=>{
                console.log(res)         
                this.posts = res.data
                let c = this.posts._meta.page
                let arr = [1,c-2,c-1,  c  ,c+1,c+2,this.posts._meta.total_pages] //7 ittems
                arr = arr.filter(item=>{
                    return item>0 && item<=this.posts._meta.total_pages
                })
                arr = [...new Set(arr)]
                if(arr.length>1 && arr[0]+1 != arr[1]){
                    arr.splice(1,0,'NaN')
                }
                if (arr.length>1 && arr[arr.length-1]-1 != arr[arr.length-2]){
                    arr.splice(arr.length-1,0,"NaN")
                }
                this.iter_pages = arr

            }).catch(e=>{
                console.log(e)
            })
        },
        test(page,index){
            console.log(this.$route.query.page)
            console.log(page,index)
        }
    },
    created:function(){
        this._getPosts()
        $(document).ready(function(){
            $("#post_body").markdown({
                autofocus:false,
                savable:false,
                iconlibrary: 'fa',  // Font Awesome
                language: 'zh'
            })
        })
        
        
    },
    beforeRouteUpdate(to,from,next){
        next()
        this._getPosts()
    }
}
</script>



